<template>
  <div class="mien container">
    <nav class="mien__nav">
      <a v-for="(item,index) in navs" :key="index"
         @click="toggleTab(item,index)"
         :class="{'active':item.path===$route.path}"
         class="mien__nav-item" href="javascript:void (0);">
        {{item.name}}
      </a>
    </nav>
    <div class="mien__content">
      <transition :name="transitionName" @afterLeave="transitionName=null">
        <router-view></router-view>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  name: 'mien',
  data() {
    return {
      transitionName: null,
      navs: [
        {
          name: '开发赞助',
          path: '/mien/sponsor'
        }, {
          name: '品牌历程',
          path: '/mien/history'
        }, {
          name: '凯发微视',
          path: '/mien/videos'
        }
      ]
    }
  },
  computed: {
    activeIndex() {
      let index = 0
      this.navs.forEach((item, i) => {
        if (item.path === this.$route.path) index = i
      })
      return index
    }
  },
  methods: {
    toggleTab(item, index) {
      if (index > this.activeIndex) {
        this.transitionName = 'slide-left'
      } else if (index < this.activeIndex) {
        this.transitionName = 'slide-right'
      }
      this.$router.replace(item.path)
    }
  }
}
</script>

<style lang="scss" scoped>
  .mien {
    &__nav {
      text-align: center;
      background-color: $--background-color-primary;
    }

    &__nav-item {
      display: inline-block;
      height: rem(50);
      padding: 0 rem(20);
      overflow: hidden;
      font-size: rem(15);
      line-height: rem(50);
      color: $--color-white;
      vertical-align: top;

      &.active {
        color: $--color-primary;

        &::after {
          display: block;
          width: 100%;
          height: 2px;
          margin-top: -2px;
          content: "\20";
          background: $--color-primary;
          border-radius: 1px;
        }
      }
    }

    &__content {
      position: absolute;
      top: rem(50);
      right: 0;
      bottom: 0;
      left: 0;
      overflow: hidden;
    }
  }
</style>
